.cards__wrapper {
  display: grid;
  grid-template-columns: 41.84% 1fr 1fr 1fr;
  grid-auto-rows: 366px;
  grid-row-gap: 30px;
  grid-column-gap: 10px;
}

.cards__img {
  display: flex;
  flex-direction: column-reverse;
  padding-bottom: 33px;
  padding-left: 41px;
  width: auto;
  height: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  span {
    @include typography(21, 25);

    width: 71%;
    font-family: $title-extra;
    color: $white;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  }

  &--history {
    grid-row: span 2;
    background-image: url("../img/israel-life-1@2x.png");

    @include max-resolution {
      background-image: url("../img/israel-life-1.png");
    }
  }

  &--language {
    margin-top: 32px;
    padding-left: 22px;
    background-image: url("../img/israel-life-2@2x.png");

    @include max-resolution {
      background-image: url("../img/israel-life-2.png");
    }
  }

  &--profession {
    grid-column: span 2;
    margin-top: 32px;
    padding-left: 31px;
    background-image: url("../img/israel-life-3@2x.png");

    @include max-resolution {
      background-image: url("../img/israel-life-3.png");
    }
  }

  &--projects {
    grid-column: span 2;
    padding-left: 31px;
    padding-bottom: 32px;
    background-image: url("../img/israel-life-4@2x.png");

    @include max-resolution {
      background-image: url("../img/israel-life-4.png");
    }

    span {
      width: 97%;
    }
  }

  &--leadership {
    padding-left: 25px;
    padding-bottom: 31px;
    background-image: url("../img/israel-life-5@2x.png");

    @include max-resolution {
      background-image: url("../img/israel-life-5.png");
    }
  }
}

@include max-desktop {
  .cards__wrapper {
    grid-template-columns: 1fr 1fr;
  }

  .cards__img {
    grid-column: span 1;
    background-position: center;

    &--history {
      grid-column: span 2;
    }

    span {
      @include typography(19, 23);
    }
  }
}

@include max-tablet {
  .cards__wrapper {
    display: flex;
    grid-gap: 0;
  }

  .cards__img {
    margin: 0;
    padding: 0;
    width: 100%;
    padding-bottom: 20px;
    min-height: 400px;

    span {
      @include typography(18, 21);

      margin: 0 auto;
      width: 90%;
      text-align: center;
    }
  }
}

